.App {
  text-align: center;
  margin: 0 10px 50px;
  font-family: Arial, Helvetica, sans-serif;
  min-width: 380px;
}

.App-titile {
  font-weight: bold;
}

.App-content {
  display: flex;
  flex-direction: column;
  max-width: 700px;
  border: 2px solid rgb(142, 144, 146);
  margin: 0 auto;
  margin-top: 10px;
}

.App-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  row-gap: 20px;

  @media (max-width: 800px) {
    flex-direction: column;
  }
}

.color-selector {
  width: 400px;
  margin: 10px 10px 10px 10px ;

  &__title {
    text-align: left;
  }

  &__ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 320px;
  }

  &__li {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 5px;
    margin-bottom: 5px;
    border: 1px solid rgb(26, 25, 25);
    width: 150px;
  }

  &__text {
    display: flex;
    justify-content: left;
    font-size: 12px;
    margin-right: 5px;
  }

  &__input {
    height: 20px;
  }
}

.axis-wrap {
  display: flex;
  flex-direction: column;
  width: 450px;

  @media (max-width: 450px) {
    width :auto;
  }
}

.X-axis-lable {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 10px;
}

.Y-axis-lable {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 10px;
}

.X-axis {
  max-width: 400px;
  height: 30px;

  &::placeholder {
    letter-spacing: 1px;
  }
}

.Y-axis {
  max-width: 400px;
  height: 30px;

  &::placeholder {
    letter-spacing: 1px;

    @media (max: 440px) {
      display: none;
    }
  }
}

.type-switcher {
  padding: 10px;
  cursor: pointer;
}

.type-switcher {
  text-align: left;
  display: flex;
  align-items: center;
}

.chart-type-button {
  margin: 10px;
  padding: 10px;
  width: 200px;
  cursor: pointer;
  border: 0;
  box-sizing: border-box;
  color: rgb(94, 175, 209);
  
  font-weight: bold;
  font-size: 16px;

  &:first-of-type {
    margin-top: 30px;
  }

  &:hover {
    box-shadow: 4px 6px 3px -4px #635e5e;
  }
  
  &--active {
    color: white;
    background-color: rgb(94, 175, 209);
  }
}

// .myChart-wrap {
// }

#myChart {
  box-sizing: border-box;
  margin: 0 50px;
}

.axis-error {
  border: 1px solid red;
}

.emptyLabelsError {
  color: red;
  font-size: 12px;
}

.matchNumberError {
  color: red;
  font-size: 12px;
}

.chart-label-wrap {
  cursor: pointer;
}

.chart-label {
  font-size: 12px;
  margin-top: 20px;
}

.triangle_icon-wrap {
  margin: 0 auto;
  width: 150px;
  height: 25px;
}

.triangle_icon {
  position: relative;
  width: 8px;
  transform: rotate(180deg);
  animation-name: move;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-timing-function: ease-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;

  &:hover {
    animation-play-state: paused;
  }
}

@keyframes move {
  from {
    top: -2px;
  }

  to {
    top: 6px;
  }
}


